<script>

    import Vue from 'vue'
    import BootstrapVue from 'bootstrap-vue'
    import { Popover } from 'bootstrap-vue/es/components';
    import { Image } from 'bootstrap-vue/es/components';
    import axios from 'axios';
    import Avatar from 'vue-avatar'

    Vue.use(BootstrapVue);
    Vue.use(Popover);
    Vue.use(Image);

    const initialData = () => {
        return {
            users: [],
            type: '',
            category: '',
            connection: 'everyone',
            selected: null,
            activeBtn: 'btn1',
            domain: 'null',
            show: false,
            selectedPeriod: 'WEEK',
            popoverShow: false,
            loadCapacity: 10


        }
    }

    export default {
        data: initialData,

        components: {
            Avatar,

        },
        directives: {
            mouseover: {
                mounted: function () {
                    jQuery('[data-toggle="popover"]').popover({
                        html: true,
                        content: $('#popover')
                    }).on('mouseenter', function () {

                        popoverShow: true;

                    })
                        .on('mouseleave', function () {

                            popoverShow: false;
                        });
                },
            }
        },
        watch: {
            domain() {
                this.loadCapacity=10
            }

        },


        methods: {
            filter() {
                let self = this
                axios.get(`/rest/gamification/gameficationinformationsboard/history/all`, { params: { 'capacity': self.loadCapacity } })
                    .then(response => {
                        this.users = response.data;

                    })
                    .catch(e => {
                        console.warn(e)

                    })

            },
            showMore() {
                let self = this
                self.loadCapacity += 10;

                axios.get(`/rest/gamification/gameficationinformationsboard/history/all`, { params: { 'capacity': self.loadCapacity } })
                    .then(response => {
                        this.users = response.data;
                    })
                    .catch(e => {
                        console.warn(e)
                    })



            },
            popOpen() {
                jQuery(".popover").popover({ trigger: "hover", html: true, animation: false })
                    .on("mouseenter", function () {
                        popoverShow: true;
                    }).on("mouseleave", function () {
                    popoverShow: false;
                });
            },

            disableByRef() {
                if (this.disabled) {
                    this.$refs.popover.$emit('enable')
                } else {
                    this.$refs.popover.$emit('disable')
                }
            },

            mouseOver() {

                jQuery(this).popover("show");


            },
            isActive(value) {
                return this.active === value
            },
            toggleClass() {
                this.isActive = !this.isActive;
            }



        },

        created() {
            axios.get(`/rest/gamification/gameficationinformationsboard/history/all`)
                .then(response => {
                    this.users = response.data;
                })
                .catch(e => {
                    this.errors.push(e)
                })
        }

    }

</script>

